export const updateGradeByTOR = ({ torId, details }) =>
  request({
    prefix,
    url: `/TestOrganizing/UpdateGradeByTOR`,
    data: {
      torId,
      details,
    },
    method: 'PUT',
  });

export const setLockedByTORId = ({ torId, isLocked }) => {
  return request({
    prefix,
    url: `/TestOrganizing/SetLockedByTORId`,
    data: {
      torId,
      isLocked,
    },
    method: 'PUT',
  });
};

export const exportExcel = torId =>
  requestDownload({
    prefix,
    url: `/TestOrganizing/ExportExcelTORById`,
    data: {
      torId,
    },
    method: 'POST',
  });

export const exportPDF = torId =>
  requestDownload({
    prefix,
    url: `/TestOrganizing/ExportTORById`,
    data: {
      torId,
    },
    method: 'POST',
  });

export const getTestOrganizings = ({
  trainTypeId,
  facultyId,
  courseId,
  studyFieldId,
  classId,
  yearEduId,
  semesterId,
  examBatch,
  examTime,
  testSessionId,
  subjectIds,
  fromDate,
  toDate,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/TestOrganizing`,
    method: 'GET',
    params: {
      trainTypeId,
      facultyId,
      courseId,
      studyFieldId,
      classId,
      yearEduId,
      semesterId,
      examBatch,
      examTime,
      testSessionId,
      subjectIds,
      fromDate,
      toDate,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
    paramsSerializer: params => qs.stringify(params, { arrayFormat: 'repeat' }),
  });

export const updateSupervisersTestOrganizing = ({
  id,
  superviser1Id,
  superviser2Id,
  superviser3Id,
} = {}) =>
  request({
    prefix,
    url: `/TestOrganizing/${id}/UpdateSupervisers`,
    method: 'PUT',
    params: {},
    data: { id, superviser1Id, superviser2Id, superviser3Id },
  });

export const deleteTestOrganizing = ({ id } = {}) =>
  request({
    prefix,
    url: `/TestOrganizing/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });

// truyen id là toId
export const getTestOrganizingById = ({ id } = {}) =>
  request({
    prefix,
    url: `/TestOrganizing/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

