import React, { Component } from 'react';
import { Card, Row, Col, Spin, message } from 'antd';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnLock, BtnExport, BtnExportPdf } from 'components/common/button';
import { getQueryParams } from 'utils/urlUtil';
import {
  getStudentInTORForGrading,
  updateGradeByTOR,
  setLockedByTORId,
  exportExcel,
  exportPDF,
} from 'services/testOrganizing';
import {
  getListDataValid
} from 'utils/formUtil';
import { makeid } from 'utils/functionUtil';

import CreditClassTable from './CreditClassTable';
import CreditClass from './CreditClass';

class InsertScorePage extends Component {
  constructor(props) {
    super(props);
    this.refForm = React.createRef();
    this.refFormRaw = React.createRef();
    this.state = {
      loading: false,
      reload: 0,
      dataScore: [],
      rowKey: 'autoKey',
    };
  }

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = async () => {
    const { loading } = this.state;
    if (loading) {
      return;
    }
    try {
      const paramSearch = getQueryParams();
      if (
        !!paramSearch.yearEduId &&
        !!paramSearch.semesterId &&
        !!paramSearch.trainTypeId &&
        !!paramSearch.facultyId &&
        !!paramSearch.subjectId &&
        !!paramSearch.examBatch &&
        !!paramSearch.torId
      ) {
        this.setState({
          loading: true,
        });
        const response = await getStudentInTORForGrading(paramSearch);
        const tempData = response.data.items.map(item => {
          if (item.gradeParts && item.gradeParts.length) {
            item.gradeParts.forEach(gradePart => {
              item[gradePart.gradePartId] = gradePart.grade;
            });
          }
          item.autoKey = `${makeid(8)}_${item.studentId}`;
          return item;
        });

        this.setState({
          dataScore: [...tempData],
        });
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleSave = async () => {
    try {
      const { dataScore } = this.state;
      if (dataScore && dataScore.length) {
        const { rowKey } = this.state;
        const listFieldName = [
          'grade',
          'gradeNoteId',
        ];

        this.setState({
          loading: true,
        });

        getListDataValid(rowKey, listFieldName, this.refForm, this.refFormRaw)
          .then(
            async (listSelectedItem) => {
              let listTemp = [...listSelectedItem];
              try {
                listTemp = listTemp.map((item) => {
                  item.grade = parseFloat(item.grade);
                  return item;
                });
                await updateGradeByTOR({
                  torId: listTemp[0].torId,
                  details: [...listTemp],
                });
                this.setState(
                  {
                    dataScore: [],
                    loading: false,
                  },
                  () => this.handleChangeQueryParams()
                );
                message.success('Lưu thành công');
              } catch (error) {
                console.log(error);
              } finally {
                this.setState({
                  loading: false,
                });
              }
            }
          )
          .catch(
            () => {
              this.setState({
                loading: false,
              });
            }
          )
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handeLock = async () => {
    try {
      const { dataScore } = this.state;
      if (dataScore && dataScore.length) {
        this.setState({
          loading: true,
        });
        await setLockedByTORId({
          torId: dataScore[0].torId,
          isLocked: !dataScore[0].isLocked,
        });
        message.success(`${dataScore[0].isLocked ? 'Mở khóa' : 'Khóa'} thành công`);
        this.setState(
          {
            dataScore: [],
            loading: false,
          },
          () => this.handleChangeQueryParams()
        );
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleExportPDF = async () => {
    const paramSearch = getQueryParams();
    try {
      if (paramSearch.torId) {
        this.setState({
          loading: true,
        });
        await exportPDF(paramSearch.torId);
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  handleExportExcel = async () => {
    const paramSearch = getQueryParams();
    try {
      if (paramSearch.torId) {
        this.setState({
          loading: true,
        });
        await exportExcel(paramSearch.torId);
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.setState({
        loading: false,
      });
    }
  };

  render() {
    const { dataScore, reload, rowKey, loading } = this.state;
    const isDisable = !(dataScore && dataScore.length);
    return (
      <Spin spinning={loading}>
        <Card title="Nhập điểm">
          <Row>
            <Col span={24}>
              <CreditClass />
            </Col>
          </Row>
          <Row>
            <Col span={24} style={{ textAlign: 'right' }}>
              <div className="btn-group">
                <BtnSave onClick={() => this.handleSave()} disabled={isDisable} />
                <BtnLock
                  title={
                    dataScore && dataScore.length && dataScore[0].isLocked ? 'Mở khóa' : 'Khóa'
                  }
                  onClick={() => this.handeLock()}
                  disabled={isDisable}
                />
                <BtnExportPdf onClick={() => this.handleExportPDF()} disabled={isDisable} />
                <BtnExport onClick={() => this.handleExportExcel()} disabled={isDisable} />
              </div>
            </Col>
          </Row>
          <Row gutter={24}>
            <Col span={24}>
              <CreditClassTable
                reload={reload}
                data={dataScore}
                ref={this.refForm}
                rowKey={rowKey}
                wrappedComponentRef={(form) => {
                  this.refFormRaw = form;
                }}
              />
            </Col>
          </Row>
        </Card>
      </Spin>
    );
  }
}

export default props => (
  <Page {...props}>
    <InsertScorePage />
  </Page>
);



