import React, { Component } from 'react';
import { Col, Row, Form } from 'antd';

import { getYearEducations } from 'services/yearEducationService';
import { getSemesters } from 'services/semesterService';
import { getTrainTypes } from 'services/trainType';
import { getFacultys } from 'services/faculty';
import { getListSubject } from 'services/Subject';
import { getTestRoomForSelect, getTestExamBatch } from 'services/testOrganizing';
import { getQueryParams, updateQueryParams } from 'utils/urlUtil';

import { SelectAjax } from 'components/common';
import { BtnSearch, BtnClear } from 'components/common/button';

@Form.create({ name: 'formCreditClass' })
class CreditClass extends Component {
  state = {};

  componentDidMount = () => {
    const { form } = this.props;
    form.setFieldsValue(getQueryParams());
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      updateQueryParams(values);
    });
  };

  handleReset = () => {
    this.props.form.resetFields();
    updateQueryParams();
  };

  render() {
    const optionFieldDecorator = {
      yearEduId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Năm học!',
          },
        ],
      },
      semesterId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học kỳ!',
          },
        ],
      },
      trainTypeId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Hệ!',
          },
        ],
      },
      facultyId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Khoa!',
          },
        ],
      },
      subjectId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Học phần!',
          },
        ],
      },
      examBatch: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Đợt thi',
          },
        ],
      },
      torId: {
        rules: [
          {
            required: true,
            message: 'Vui lòng chọn Phòng thi',
          },
        ],
      },
    };
    const { getFieldDecorator, setFieldsValue, getFieldValue } = this.props.form;
    const yearEduId = getFieldValue('yearEduId');
    const semesterId = getFieldValue('semesterId');
    const trainTypeId = getFieldValue('trainTypeId');
    const facultyId = getFieldValue('facultyId');
    const subjectId = getFieldValue('subjectId');
    const examBatch = getFieldValue('examBatch');
    const responsiveLayout = {
      sm: { span: 24 },
      md: { span: 12 },
      lg: { span: 6 },
    };
    return (
      <Form layout="vertical" onSubmit={this.handleSearch} className="form-inline-label">
        <Row gutter={24} justify="space-between">
          <Col {...responsiveLayout}>
            <Form.Item label="Năm học:">
              {getFieldDecorator(
                'yearEduId',
                optionFieldDecorator.yearEduId
              )(
                <SelectAjax
                  service={getYearEducations}
                  setLabel={yearEdu => `${yearEdu.fromYear}-${yearEdu.toYear}`}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col {...responsiveLayout}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator(
                'semesterId',
                optionFieldDecorator.semesterId
              )(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getSemesters}
                  params={{ yearEduId }}
                  allowGetData={!!yearEduId}
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col {...responsiveLayout}>
            <Form.Item label="Hệ">
              {getFieldDecorator(
                'trainTypeId',
                optionFieldDecorator.trainTypeId
              )(<SelectAjax service={getTrainTypes} />)}
            </Form.Item>
          </Col>
          <Col {...responsiveLayout}>
            <Form.Item label="Khoa">
              {getFieldDecorator(
                'facultyId',
                optionFieldDecorator.facultyId
              )(
                <SelectAjax
                  setFieldsValue={setFieldsValue}
                  service={getFacultys}
                  params={{ trainTypeId }}
                  allowGetData={!!trainTypeId}
                />
              )}
            </Form.Item>
          </Col>
          <Col {...responsiveLayout}>
            <Form.Item label="Học phần">
              {getFieldDecorator(
                `subjectId`,
                optionFieldDecorator.subjectId
              )(<SelectAjax setFieldsValue={setFieldsValue} service={getListSubject} />)}
            </Form.Item>
          </Col>
          <Col {...responsiveLayout}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator(
                'examBatch',
                optionFieldDecorator.examBatch
              )(
                // <InputNumber min={1} max={4} style={{ width: '100%' }} />
                <SelectAjax
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                  service={getTestExamBatch}
                  params={{
                    trainTypeId,
                    facultyId,
                    yearEduId,
                    semesterId,
                    subjectId,
                  }}
                  allowGetData={
                    !!yearEduId && !!semesterId && !!trainTypeId && !!facultyId && !!subjectId
                  }
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col {...responsiveLayout}>
            <Form.Item label="Phòng thi">
              {getFieldDecorator(
                'torId',
                optionFieldDecorator.torId
              )(
                <SelectAjax
                  keyValue="value"
                  keyLabel="label"
                  setFieldsValue={setFieldsValue}
                  service={getTestRoomForSelect}
                  params={{
                    yearEduId,
                    semesterId,
                    trainTypeId,
                    facultyId,
                    subjectId,
                    examBatch,
                  }}
                  allowGetData={
                    !!yearEduId &&
                    !!semesterId &&
                    !!trainTypeId &&
                    !!facultyId &&
                    !!subjectId &&
                    !!examBatch
                  }
                  typeValue="int"
                />
              )}
            </Form.Item>
          </Col>
          <Col span={4} style={{ textAlign: 'left' }}>
            <div className="btn-group">
              <BtnSearch htmlType="submit" />
              <BtnClear onClick={this.handleReset} />
            </div>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default CreditClass;



